<template>
  <a-modal v-if="canEdit" cancel-text="Отменить" v-model:visible="viewEditColumnName" title="Изменить название"
           @ok="changeTableColumns">

    <a-input v-if="currentColumn" v-model:value="currentColumn.name"></a-input>

  </a-modal>
  <v-grid class="table" @afteredit="changeTableData" :auto-size-column="true" theme="material" :resize="true"
          :columns="tableColumns" :source="tableData"></v-grid>

</template>

<script>
import VGrid from "@revolist/vue3-datagrid";
import {computed, onBeforeUnmount, ref, watch} from "vue";
import {useStore} from "vuex";
import {appConfig} from "@/lib/appConfig";

export default {
  props: ['hackathonId', 'canEdit'],
  name: 'HackathonTable',
  components: {
    VGrid,
  },
  setup(props) {
    const store = useStore();

    const hackathon = computed(() => store.getters.hackathon);

    const currentColumn = ref(null);

    const viewEditColumnName = ref(false);

    const socket = ref(null)
    if (props.hackathonId !== 'new') {
      socket.value = new WebSocket(appConfig.socket);
      socket.value.onopen = () =>
          socket.value.onmessage = (msg) => {
            try {
              const data = JSON.parse(msg.data)
              if (data?.action === 'HACKATON_UPDATE_MESSAGE') {
                store.commit('updateTableCell', data?.msg?.cells[0]);
              }
            } catch (e) {
              console.log(e);
            }
          }
    }

    const tableColumns = computed(() => {
      return hackathon?.value?.table ? hackathon.value.table?.filter(item => !item?.y_coord).toSorted((a, b) => a?.x_coord - b?.x_coord).map(item => {
        return {
          name: item?.body,
          prop: `x${item?.x_coord}`,
          id: item?.id,
          size: 300,
          columnTemplate: (createElement, column) => {
            const columnElements = [createElement('span', {}, column.name)];
            if (props.canEdit) {
              columnElements.push(createElement('button', {
                onclick: () => {
                  currentColumn.value = column;
                  viewEditColumnName.value = true;
                },
              }, '(Ред.)'))
            }
            return columnElements;
          },
        }
      }) : [];
    });

    const tableData = computed(() => {
      return hackathon?.value?.table ? [...new Map(hackathon.value.table.filter(item => item?.y_coord).toSorted((a, b) => a?.y_coord - b?.y_coord).map((item, index, array) => {
        const currentYCells = array.filter(cell => cell?.y_coord === item?.y_coord);
        const newObj = {
          y_coord: item?.y_coord,
        }
        for (let i = 0; i < currentYCells.length; i++) {
          newObj[`x${currentYCells[i]?.x_coord}`] = currentYCells[i]?.body;
          newObj[`x${currentYCells[i]?.x_coord}Id`] = currentYCells[i]?.id;
        }
        return newObj
      }).map(item => [item?.y_coord, item])).values()] : [];
    });

    const changeTableData = (event) => {

      const cell = {
        id: event?.detail?.model?.[`${event?.detail?.prop}Id`],
        x_coord: Number(event?.detail?.prop?.replace('x', '')),
        y_coord: event?.detail?.rowIndex + 1,
        body: event?.detail?.val
      }

      if (socket.value) {
        store.dispatch('updateTableCell', {socket: socket.value, cell, hackatonId: props.hackathonId});
      } else {
        store.commit('updateTableCell', cell);
      }
    }

    const changeTableColumns = () => {
      const cell = {
        id: currentColumn.value?.id,
        y_coord: 0,
        x_coord: Number(currentColumn?.value.prop.replace('x', '')),
        body: currentColumn.value?.name
      }

      if (socket.value) {
        store.dispatch('updateTableCell', {socket: socket.value, cell, hackatonId: props.hackathonId});
      } else {
        store.commit('updateTableCell', cell);
      }

      viewEditColumnName.value = false;
    }


    onBeforeUnmount(() => socket.value?.close());

    watch(() => viewEditColumnName.value, (val) => {

      if (!val) currentColumn.value = null;
    })

    return {
      tableColumns,
      tableData,
      currentColumn,
      viewEditColumnName,

      changeTableData,
      changeTableColumns
    }
  }
}


</script>


<style scoped>
.table {
  max-height: 1000px;
  min-height: 500px;
}
</style>