<template>
  <div class="hackathon-container">
<!--    <div class="hackathon-description">-->
<!--      <a-typography-text>{{ hackathon?.description }}</a-typography-text>-->
<!--      <a-divider></a-divider>-->
<!--    </div>-->
    <hackathon-table :can-edit="false" :hackathon-id="hackathonId"></hackathon-table>
  </div>
</template>

<script>
import HackathonTable from "@/components/hackathon/HackathonTable.vue";
import {useStore} from "vuex";
import {computed, watch} from "vue";

export default {
  name: 'HackathonPage',
  props: ['hackathonId'],
  components: {HackathonTable},
  setup(props) {
    const store = useStore();
    const hackathon = computed(() => store.getters.hackathon);
    store.dispatch('fetchHackathonById', props.hackathonId);
    watch(() => hackathon.value, (val) => {
      document?.dispatchEvent(new CustomEvent('setTitle', {detail: val?.name}))
    })
    return {
      hackathon
    }
  }
}

</script>


<style scoped>
.hackathon-container {
  padding: 24px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
